import React, { useState } from 'react';
import { TextField, Button } from '@mui/material';

export default function Research({ searchTerm, setSearchTerm }) {
    const [localSearchTerm, setLocalSearchTerm] = useState(searchTerm); // tat local pour la barre de recherche

    const handleChange = (event) => {
        setLocalSearchTerm(event.target.value); // Met  jour l'tat local
    };

    const handleSearch = () => {
        setSearchTerm(localSearchTerm); // Met  jour le terme de recherche parent
    };

    return (
        <div className="Research" style={{ marginTop: '5%', display: 'flex', justifyContent: 'center', alignItems: 'center',marginBottom:'3%' }}>
            <TextField
                label="Rechercher un Pokmon"
                variant="outlined"
                value={localSearchTerm} // Liaison  l'tat local de recherche
                onChange={handleChange} // Gestionnaire d'vnement pour le changement de texte
                sx={{ width: '50%' }} // Pour occuper 50% de la largeur
            />
            <Button 
                variant="contained" 
                color="primary" 
                onClick={handleSearch} // Gestionnaire d'vnements pour le clic
                style={{ marginLeft: '10px' }} // Espacement  gauche
            >
                Valider
            </Button>
        </div>
    );
}
